<template>
  <div>
    <div class="home-title">Personal Boards</div>
    <div>
      <div class="board-list" ref="boardList">
        <div class="board-item" v-for="b in boards" :key="b.id" :data-bgcolor="b.bgColor" ref="boardItem">

          <router-link :to="`/study/trello/${b.id}`">
            <div class="board-item-title">{{b.title}}</div>
          </router-link>
        </div>
        <div class="board-item board-item-new">
          <a class="new-board-btn" href="" @click.prevent="addBoard">
            Create new board...
          </a>
        </div>
      </div>
    </div>

   <AddBoard v-if="isAddBoard" @close="isAddBoard =false" @submit="onAddBoard"></AddBoard>

  </div>
</template>

<script>
  //import { board } from '../../api'

  import axios from 'axios'
  import AddBoard from './AddBoard'
import { setTimeout } from 'core-js';


  export default {
    components: { AddBoard},
    data() {
      return {
        loading: false,
        boards: [],
        error: '',
        isAddBoard : false
      }
    },
    created() {
      this.fetchData()
    },
    updated() {
      //created 다음 호출, data 변화 인지되면 실행
      this.$refs["boardItem"].forEach(el => {
        el.style.backgroundColor = el.dataset.bgcolor
      })
    },
    methods: {
      fetchData() {
        this.loading = true
        var ls = localStorage;

        if (helper.isNull(ls.getItem("boards"))) return;
        
        setTimeout(() => {
          this.boards = JSON.parse(ls.getItem("boards"));
        }), 1;

      },
      addBoard() {
        this.isAddBoard = true
        
      },
      onAddBoard(title) {
        //새로운 보드 호출하려면 api호출



        var ls = localStorage;
        var obj = {};
        var arr = [];

        obj.title = title;
        obj.bgColor = "rgb(0,121,191)";
        arr.push(obj);

        if (!helper.isNull(localStorage.boards)) {
          var boards = JSON.parse(ls.boards);
          boards.forEach(el => {
            arr.push(el);
          })
        }


        ls.setItem("boards", JSON.stringify(arr));

        setTimeout(() => {
          this.boards = JSON.parse(ls.boards);
        }), 100;

      }
    }
  }
</script>

<style>
  .home-title {
    padding: 10px;
    font-size: 18px;
    font-weight: bold;
  }

  .board-list {
    padding: 10px;
    display: flex;
    flex-wrap: wrap;
  }

  .board-item {
    width: 23%;
    height: 100px;
    margin: 0 2% 20px 0;
    border-radius: 3px;
  }

  .board-item-new {
    background-color: #ddd;
  }

  .board-item a {
    text-decoration: none;
    display: block;
    width: 100%;
    height: 100%;
  }

    .board-item a:hover,
    .board-item a:focus {
      background-color: rgba(0,0,0, .1);
      color: #666;
    }

  .board-item-title {
    color: #fff;
    font-size: 18px;
    font-weight: 700;
    padding: 10px;
  }

  .board-item a.new-board-btn {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
    height: 100px;
    width: inherit;
    color: #888;
    font-weight: 700;
  }
</style>
